import React, { useState,createContext,useEffect } from "react";
import PropTypes from "prop-types";
import axios from "axios";

// core components
import Header from "../../components/header/header.jsx";
import Footer from "../../components/footer/footer.jsx";

// sections for this page
import FeatureComponent from "./sections/featurecomponent.jsx";
import PortfolioComponent from "./sections/portfoliocomponent.jsx";
import PricingComponent from "./sections/pricingcomponent.jsx";
import TestimonialComponent from "./sections/testimonialcomponent.jsx";
import ContactComponent from "./sections/contactcomponent.jsx";

export const globalStateOfAuthentication = createContext();
const CustomComponents = (props) => {


    const [modal, setModal] = useState(false);
    const [modal1, setModal1] = useState(false);
    const [modal2, setModal2] = useState(false);
    const [globalAuthenticated,setGlobalAuthenticated]=useState(false);

    
    const authentificationVerification=async()=>{
        axios.defaults.withCredentials = true;
        await axios.get("http://154.56.57.8/api/isAuthenticated").then((response)=>{
            setGlobalAuthenticated(true);
        })
        .catch((error)=>{
            setGlobalAuthenticated(false);
            axios.defaults.withCredentials = true;
            axios.get('http://154.56.57.8/api/sanctum/csrf-cookie').then(response => {
            console.log(response);
            console.clear();

            });
        })
    }

    const toggle = () => {
        setModal(!modal);
    }

    const toggle1 = () => {
        setModal1(!modal1);
    }

    const toggle2 = () => {
        setModal2(!modal2);
    }

    useEffect(() => {
        authentificationVerification();
       
      }, []);

    return (

        <globalStateOfAuthentication.Provider value={globalAuthenticated}>
        
      

        <div id="main-wrapper">
            <div id="Accueil"><Header /></div>
            


            <div className="page-wrapper">
                <div className="container-fluid">
                    
                    
                    <div id="NosPlans"><PricingComponent /></div>
                    <TestimonialComponent />
                    <div id="NosServices"><FeatureComponent /></div>
                    <PortfolioComponent />
                    <div id="ContactUs"><ContactComponent /></div>
                    
                    
                    
                </div>
            </div>
            <Footer />
        </div>
        </globalStateOfAuthentication.Provider>
    );
}

CustomComponents.propTypes = {
    classes: PropTypes.object
};

export default CustomComponents;
