/* eslint-disable */
import {React,useState,useEffect,useContext} from 'react';
import { Button,Row, Col, Container, Card, CardBody,Modal,ModalHeader,ModalBody
        ,FormGroup,Input,Label,UncontrolledDropdown,DropdownToggle,DropdownMenu,DropdownItem, Alert } from 'reactstrap';
import axios from 'axios';
import {globalStateOfAuthentication} from "./../custom-components"

const PricingComponent = () => {


    const [plans, setPlans] = useState([]);
    const [selectedPlan,setSelectedPlan]=useState(0);
    const [selectedPlanName,setSelectedPlanName]=useState("");
    const [domaineSite,setDomaineSite]=useState("");
    const [typeSite,setTypeSite]=useState("");
    const [nomSite,setNomSite]=useState("");
    const [descSite,setDescSite]=useState("");
    const [modal1, setModal1] = useState(false);
    const AuthenticationState = useContext(globalStateOfAuthentication);
    const toggle1 = () => {
        setModal1(!modal1);
        setSelectedPlan(1);
        setSelectedPlanName("Economique");
    }
    const getPlans = async () => {
      const { data } = await axios.get("http://154.56.57.8/api/Plans");
      setPlans(data);
    }

    const handleChange = (event) => {
        setSelectedPlan(event.target.value);
        setSelectedPlanName(event.target.name);
    };

    const submitCommand=async()=>{
        await axios
        .post("http://154.56.57.8/api/submitCommande", {
            domaine:domaineSite,
            nomSite:nomSite,
            typeSite:typeSite,
            descSite:descSite,
            id_plan:selectedPlan,

        })
        .then((response) => {
          console.log(response);
          window.location.reload(false);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  
    useEffect(() => {
      getPlans();
    }, []);


    return (
        <div>
            
            <Modal size="lg" isOpen={modal1} toggle={toggle1.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle1.bind(null)}>Commande platform</ModalHeader>
                            <ModalBody>
                            {!AuthenticationState ? <Alert color="danger">Veuillez vous authentifiez our crer un compte pour raliser des commandes.</Alert> :
                            <>
                            <UncontrolledDropdown className="mx-1 my-3">
                            <DropdownToggle  className="btn-info" caret>
                            Plan
                            </DropdownToggle>
                            <DropdownMenu >
                            {plans.map(plan=>{
                                return(
                                    <DropdownItem key={plan.id} value={plan.id} name={plan.nom_plan} onClick={handleChange}>{plan.nom_plan}</DropdownItem>
                                );
                                }
                            )}
                        
                            </DropdownMenu>
                            </UncontrolledDropdown> <strong className="me-auto">Plan selectionner : <span className="text-success">{selectedPlanName}</span></strong>
                            <hr></hr>
                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="domaine">Nom de domaine</Label>
                                <Input type="text" className="form-control"  placeholder="ex:domaine.com" 
                                onChange={(e) => setDomaineSite(e.target.value)}/>
                            </FormGroup>
                            </Row>
                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="Nom">Nom du site</Label>
                                <Input type="text" className="form-control"  placeholder="Nom de votre site" 
                                onChange={(e) => setNomSite(e.target.value)}/>
                            </FormGroup>
                            </Row>
                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="type">Type du site</Label>
                                <Input type="text" className="form-control"  placeholder="Type de votre site" 
                                onChange={(e) => setTypeSite(e.target.value)}/>
                            </FormGroup>
                            </Row>
                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="description">Desription du site</Label>
                                <Input type="textarea" className="form-control"  placeholder="Description de votre site" 
                                onChange={(e) => setDescSite(e.target.value)}/>
                            </FormGroup>
                            </Row>
                            <Button type="submit" onClick={submitCommand} className="btn btn-success waves-effect waves-light m-r-10">Commander</Button>
                            </>
                                }
                            

                            
                            </ModalBody>
                            
                        </Modal>


            <div className="pricing8 spacer b-t">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h2 className="title">Nos prix pour construire vos sites</h2>
                            <h6 className="subtitle">nous offrons un service de qualit avec 100% satisfaction</h6>
                        </Col>
                    </Row>
                    <Row className="m-t-40">

                    {plans.map((plan) => (
                        <Col md="4" className="ms-auto pricing-box align-self-center" key={plan.id}>
                            <Card className="b-all">
                                <CardBody className="p-30 text-center">
                                    <h5>Plan {plan.nom_plan}</h5>
                                    <sup>$</sup><span className="text-dark display-5">{plan.prix_mensuel_plan}</span>
                                    <h6 className="font-light font-14">Mensuel</h6>
                                    <p className="m-t-40">Nombre de pages {plan.nbr_page}, nombre de modification par anne {plan.nbr_modification}</p>
                                </CardBody>
                                <a className="btn btn-info-gradiant p-15 btn-arrow btn-block" href="#" onClick={toggle1.bind(null)}>CHOISIR LE PLAN</a>
                            </Card>
                        </Col>
        ))}

                    </Row>
                </Container>
            </div>
        </div>
    );
}

export default PricingComponent;
