import React, { useState,useEffect,useContext} from "react";
import { Button,Form,Input,ButtonGroup,UncontrolledDropdown,DropdownToggle,DropdownMenu
,DropdownItem,
Col,Row,Modal, ModalHeader, ModalBody, Alert } from "reactstrap"; 
import {globalStateOfAuthentication} from "../../views/custom-components/custom-components.jsx"
import axios from "axios";
function TicketPlatform ({siteConnecteds}){
    const [contenuTicket, setContenuTicket] = useState("");
    const [titre, setTitre] = useState("");
    const [niveauUrgence, setNiveauUrgence] = useState(1);
    const [idSite, setidSite] = useState(1);
    const [tickets,setTicket]=useState([]);
    const [ticketConfirmation,setTicketConfirmation]=useState(false);
    const AuthenticationState = useContext(globalStateOfAuthentication);
    

    const ticketList=async()=>{

      const { data } = await axios.get("http://154.56.57.8/api/getTickets");
      setTicket(data);

    }


    const handleChange = (event) => {
      setidSite(event.target.value);
    };

  const data = {
        contenuTicket,
        titre,
        niveauUrgence,
        idSite,
      };

      const handleSubmit=async(e)=>{
        e.preventDefault();
        axios.post('http://154.56.57.8/api/submitTicket', data).then((response)=>{
        console.log(response);
        ticketList();
        setTicketConfirmation(true);
    })
    setTitre("");
    setContenuTicket("");
    setNiveauUrgence("");
    setidSite(0); 
    }
    useEffect(() => {
      ticketList();
    }, []);

    return(
        <>
        {!AuthenticationState ? <Alert>Oups il parait que vous-avez consulter le mauvais chemin</Alert>
        
      :
      <>
                        
        <div className="text-center"><h3>Mes tickets
      </h3></div>
      <br></br>
      
        <Row>
          <Col className="col-lg-5"><h6 className="text-primary"><small>Sujet</small></h6></Col>
          <Col ><h6 className="text-primary"><small>Etat du ticket</small></h6></Col>
          <Col ><h6 className="text-primary"><small>Niveau d'urgence</small></h6></Col>
          <Col><h6 className="text-primary text-center"><small>Link</small></h6></Col>
          </Row>

        
          {tickets.map(ticket=>{
            return(
              <>
              <Row className="py-0" key={ticket.id}>
              <Col className="col-lg-5"><p className="text-dark">{ticket.titre}</p></Col>
              <Col className="text-center">{ticket.Etat===0 ? <span className="badge bg-success">Ouverte</span> : <span className="badge bg-danger">Fermer</span>}</Col>
              <Col className="text-center">{ticket.niveauUrgence ===3 ? <span className="badge bg-danger">Critique</span> : ""}{ticket.niveauUrgence ===2 ? <span className="badge bg-warning">Moyen</span> : ""}{ticket.niveauUrgence ===1 ? <span className="badge bg-info">Normal</span> : ""}</Col>
              <Col><a href={"#/TicketDetails?ticketId="+ticket.id} target="_blank" className="btn btn-info" role="button">Consulter</a></Col>

              </Row>
              <hr className="my-1"></hr>
              </>
            );

          })}
        <br></br>
        <div className="text-center"><h3>Rdiger un ticket
      </h3></div>
      <br></br>
      {ticketConfirmation ? <Alert>Nous avons bien reu votre ticket</Alert> : ""}
        <Form  method="POST">
                        
                        <Input type="hidden" name="_token" value="{{ csrf_token() }}" />
        
        <Row className="mx-3 my-1">
        
        <Col className="col-sm-1">
        <Input
        className='bg-success'
          type="radio"
          name="niveauUrgence"
          value="1"
          checked={niveauUrgence === 1}
          onChange={(e) => setNiveauUrgence(1)}
        />
        </Col>
        <Col><h6>Faible</h6></Col>
        </Row>
        <Row className="mx-3 my-1">
        
        <Col className="col-sm-1">
        <Input
        className='bg-warning'
          type="radio"
          name="niveauUrgence"
          value="1"
          checked={niveauUrgence === 2}
          onChange={(e) => setNiveauUrgence(2)}
        />
        </Col>
        <Col><h6>Moyen</h6></Col>
        </Row>
        <Row className="mx-3 my-1">
        
        <Col className="col-sm-1">
        <Input
        className='bg-danger'
          type="radio"
          name="niveauUrgence"
          value="1"
          checked={niveauUrgence === 3}
          onChange={(e) => setNiveauUrgence(3)}
        />
        </Col>
        <Col><h6>Critique</h6></Col>
        </Row>
                        
            <UncontrolledDropdown className="mx-1 my-3">
            <DropdownToggle  className="btn-info" caret>
            Mes sites
            </DropdownToggle>
            <DropdownMenu >
            {siteConnecteds.map(site=>{
                return(
                    <DropdownItem key={site.id} value={site.id} onClick={handleChange}>{site.nom_site}</DropdownItem>
                );
                }
            )}
        
            </DropdownMenu>
            </UncontrolledDropdown>
            <hr></hr>
            
            <Input
        className='h-75 mx-1 my-3'
        type="text"
        placeholder="Sujet"
        onChange={(e) => setTitre(e.target.value)}
      />
            <Input
        className='h-75 mx-1 my-3'
        type="textarea"
        placeholder="Votre message"
        onChange={(e) => setContenuTicket(e.target.value)}
      />
      <hr></hr>
      <Button className="mx-1" onClick={handleSubmit}>Envoyer</Button>
      </Form>
      </>
        }
    </>
    );


}
export default TicketPlatform;