import React, { useState,useEffect,useRef } from "react"
import axios from "axios";
import { Route, Routes, json, useNavigate } from 'react-router-dom';
import ReviewForm from '../review/reviewComponent.jsx';
import { Badge,Spinner,Row,Col,DropdownItem,DropdownToggle,DropdownMenu,UncontrolledDropdown,Modal, ModalHeader, ModalBody } from "reactstrap";
import TicketPlatform from "../ticketPlatform/ticketPlatForm.jsx";
function AuthenticatedComponent ({emailConnected,sitesConnected,commandesConnected}){
  const [modal1, setModal1] = useState(false);
  const [modal2, setModal2] = useState(false);
  const [modal3, setModal3] = useState(false);
  const [modal4, setModal4] = useState(false);
  const toggle1 = () => {
      setModal1(!modal1);
  }
  const toggle2 = () => {
      setModal2(!modal2);
  }
  const toggle3 = () => {
    setModal3(!modal3);
}
const toggle4 = () => {
  setModal4(!modal4);
}
    const navigate=useNavigate();
    const ClickForLogOut = async()=>{
        await axios
        .get("http://154.56.57.8/api/Logout")
        .then((response) => {
          console.log(response);
          window.location.reload(false);
        })
        .catch((error) => {
          console.log(error);
        });
    }


    return( 
    <>
    <UncontrolledDropdown nav inNavbar>
      <DropdownToggle nav>
        Espace Client <i className="fa fa-angle-down m-l-5"></i>
      </DropdownToggle>
      <DropdownMenu className="b-none animated fadeInUp bg-dark">
        <DropdownItem onClick={toggle2.bind(null)}>Mes Sites</DropdownItem>
        <DropdownItem onClick={toggle4.bind(null)}>Mes Commandes</DropdownItem>
        <DropdownItem onClick={toggle1.bind(null)}>Ecrire Review</DropdownItem>
        <DropdownItem onClick={toggle3.bind(null)}>Tickets support</DropdownItem>
        <DropdownItem divider />
        <DropdownItem onClick={ClickForLogOut}>Logout </DropdownItem>
      </DropdownMenu>
    </UncontrolledDropdown>
    <h6 className="text-info text-center">En ligne <br></br><i className="fa fa-user fa-2x mx-2 text-success"></i>{emailConnected.slice(1, -1)}</h6>
    
    
    <Modal size="lg" isOpen={modal3} toggle={toggle3.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle3.bind(null)}>Ticket platform</ModalHeader>
                            <ModalBody>
                            
                            <TicketPlatform siteConnecteds={sitesConnected}/>
                            </ModalBody>
                            
                        </Modal>

                        


      
                        <Modal  isOpen={modal1} toggle={toggle1.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle1.bind(null)}>Enregistrer un review</ModalHeader>
                            <ModalBody>
                            
                            <ReviewForm/>
                            </ModalBody>
                            
                        </Modal>

                        <Modal  isOpen={modal4} toggle={toggle4.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle4.bind(null)}>Mes Commandes</ModalHeader>
                            <ModalBody>
                            
                            
                            <div>
                            <Row>
                              <Col><p className="text-primary">Nom du site</p></Col>
                              <Col className="lg4"><p className="text-primary">domaine</p></Col>
                              <Col><p className="text-primary">Type site</p></Col>
                              <Col ><p className="text-primary">Etat</p></Col>
                              </Row>
                          {commandesConnected.map(maCommande => {
                            return (
                              <div key={maCommande.id}>
                              
                              <Row>
                              <Col><p className="text-dark">{maCommande.nomSite}</p></Col>
                              <Col><p className="text-dark">{maCommande.domaine}</p></Col>
                              <Col><span className="label label-info">{maCommande.typeSite}</span></Col>
                              <Col>{maCommande.Etat===0 ? <Badge color="warning">En cours</Badge> : <Badge color="success">Traite</Badge>}</Col>
                              </Row>
                              
            

            <hr />
          </div>
        );
      })}
    </div>


                            </ModalBody>
                            
                        </Modal>


    <Modal size="md" isOpen={modal2} toggle={toggle2.bind(null)} >
                            <ModalHeader toggle={toggle2.bind(null)}>Mes sites</ModalHeader>
                            <ModalBody>

                            <div>
                            <Row>
          <Col><p className="text-primary">Nom du site</p></Col>
          <Col className="lg4"><p className="text-primary">domaine</p></Col>
          <Col><p className="text-primary">Type site</p></Col>
          <Col ><p className="text-primary">Link</p></Col>
          </Row>
      {sitesConnected.map(monSite => {
        return (
          <div key={monSite.id}>
          
          <Row>
          <Col><p className="text-dark">{monSite.nom_site}</p></Col>
          <Col><p className="text-dark">{monSite.domaine_site}</p></Col>
          <Col><span className="label label-info">{monSite.type_site}</span></Col>
          <Col><a href={monSite.domaine_site}><i className="fa fa-external-link" aria-hidden="true"></i></a></Col>
          </Row>
          
            

            <hr />
          </div>
        );
      })}
    </div>
                            </ModalBody>
                            
                        </Modal>
    
    </>
    )
    
}
export default AuthenticatedComponent