import React,{ useRef,useState } from 'react';
import { Alert,Container, Row, Col, Form, FormGroup, Label, Input, Button } from 'reactstrap';
import axios from "axios";



const FormRegister = () => {
    const [client, setClient]=useState({
        email: "",
        password: "",
        nomClient:"",
        prenomClient:""

});
    const [created,setCreated]=useState(true);
    
    const handleInputChange =(evt)=>{
        const value = evt.target.value;
  
        setClient({
      
          ...client,
      
          [evt.target.name]: value
      
        });
    }

    const handleSubmit = async (event) => {
        event.preventDefault();
        var dataClient = new FormData();
        dataClient.append('email', client.email);
        dataClient.append('password', client.password);
        dataClient.append('nomClient', client.nomClient);
        dataClient.append('prenomClient', client.prenomClient);
        axios.defaults.withCredentials = true;



          axios
        .post("http://154.56.57.8/api/register", dataClient)
        .then((response) => {
          console.log(response);
          window.location.reload(false);

        })
        .catch((error) => {
            axios.defaults.withCredentials = true;
            axios.get('http://154.56.57.8/api/sanctum/csrf-cookie').then(response => {
            console.log(response);
            console.clear();

            });
          console.log(error);
          setCreated(false);
        });


    
    
    }
    return (
        <div>
            
            <Container>
                
                        <Form onSubmit={handleSubmit} method="POST">
                        
                        <Input type="hidden" name="_token" value="{{ csrf_token() }}" />
                        {created ? "" : <Alert color="danger" >
                            Erreur lors de la creation du compte
                        </Alert> }
                        
                        <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="Nom">Nom</Label>
                                <Input type="text"  className="form-control" name="nomClient" id="nomClient" value={client.nomClient} onChange={handleInputChange}  placeholder="Votre Nom" />
                            </FormGroup>
                            </Row>
                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="email">Prnom</Label>
                                <Input type="text" className="form-control" name="prenomClient" id="prenomClient" value={client.prenomClient} onChange={handleInputChange}  placeholder="Votre Prnom" />
                            </FormGroup>
                            </Row>

                            <Row>
                            <FormGroup className="col" >
                                <Label htmlFor="email">Email Address</Label>
                                <Input type="email" className="form-control" name="email" id="email" value={client.email} onChange={handleInputChange}  placeholder="Enter email" />
                            </FormGroup>
                            </Row>
                            <Row>
                            <FormGroup className="col">
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" className="form-control" id="password" name="password" value={client.password} onChange={handleInputChange}  placeholder="Password" />
                            </FormGroup>
                            </Row>
                            
                            <Col md="12">
                                <Button type="submit" className="btn btn-success waves-effect waves-light m-r-10">Crer Compte</Button>
                            </Col>
                        </Form>
                    
            </Container>
        </div>
    );
}

export default FormRegister;
