import React, { useState } from "react";
import {
  Alert,
  Row,
  Col,
  Container,
  Form,
  FormGroup,
  Input,
  Button,
  Modal,ModalBody,ModalHeader
} from "reactstrap";
import axios from "axios";

const ContactComponent = () => {
  const [emailGuest,setEmailGuest]=useState("");
  const [nameGuest,setNameGuest]=useState("");
  const [contenuMessage,setContenuMessage]=useState("");
  const [messageEnvoyer,setMessageEnvoyer]=useState(false);
  const [modal5, setModal5] = useState(false);
  const toggle5 = () => {
    setModal5(!modal5);
}

  const submitContact=async()=>{
    axios.defaults.withCredentials = true;
    await axios
    
        .post("http://154.56.57.8/api/submitContact", {
          nomComplet:nameGuest,
          email:emailGuest,
          messageContenu:contenuMessage
        })
        .then((response) => {
          console.log(response);
          setMessageEnvoyer(true);
         
        })
        .catch((error) => {
          console.log(error);
          setMessageEnvoyer(false);
        });
        toggle5();
        setContenuMessage("");
        setEmailGuest("");
        setNameGuest("");
  }



  return (
    <div>



<Modal  isOpen={modal5} toggle={toggle5.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle5.bind(null)}>Confirmation contact</ModalHeader>
                            <ModalBody>
                            
                            {messageEnvoyer? <Alert>Nous avons bien reu votre message</Alert> : <Alert color="danger">Erreur d'envoi du message</Alert> }
                            </ModalBody>
                            
                        </Modal>


      <div className="spacer bg-light">
        <Container>
          <Row className="justify-content-center">
            <Col md="7" className="text-center">
              <h1 className="title font-bold">Get In Touch</h1>
              <h6 className="subtitle">
                Here you can check Demos we created based on WrapKit. Its quite
                easy to Create your own dream website &amp; dashboard in
                No-time.
              </h6>
            </Col>
          </Row>
        </Container>
      </div>
      <div className="contact1">
        <Container>
          <Row>
            <div className="spacer">
              <Row className="m-0">
                <Col lg="8">
                  <div className="contact-box p-r-40">
                    <h4 className="title">Quick Contact</h4>
                    
                      <Row>
                        <Col lg="6">
                          <FormGroup className="m-t-15">
                            <Input type="text" placeholder="name" 
                            value={nameGuest}
                            onChange={(e) => setNameGuest(e.target.value)}/>
                          </FormGroup>
                        </Col>
                        <Col lg="6">
                          <FormGroup className="m-t-15">
                            <Input type="text" placeholder="email" 
                            value={emailGuest}
                            onChange={(e) => setEmailGuest(e.target.value)}/>
                          </FormGroup>
                        </Col>
                        <Col lg="12">
                          <FormGroup className="m-t-15">
                            <Input
                              type="textarea"
                              
                              placeholder="message"
                              value={contenuMessage}
                              onChange={(e) => setContenuMessage(e.target.value)}
                            />
                          </FormGroup>
                        </Col>
                        <Col lg="12">
                          <Button
                            
                            className="btn btn-danger-gradiant m-t-20 btn-arrow" onClick={submitContact}
                          >
                            <span>
                              
                              SUBMIT <i className="ti-arrow-right"></i>
                            </span>
                          </Button>
                        </Col>
                      </Row>
                    
                  </div>
                </Col>
                <Col lg="4">
                  <div className="detail-box p-40 bg-info">
                    <h2 className="text-white">Wrappixel Headquarters</h2>
                    <p className="text-white m-t-30 op-8">
                      251 546 9442
                      <br /> info@wrappixel.com
                    </p>
                    <p className="text-white op-8">
                      601 Sherwood Ave.
                      <br /> San Bernandino, CA 92404
                    </p>
                  </div>
                </Col>
              </Row>
            </div>
          </Row>
        </Container>
      </div>
    </div>
  );
};

export default ContactComponent;
