import {Modal, ModalHeader, ModalBody,Button,NavItem, Spinner} from 'reactstrap';
import { useState,React} from 'react';
import FormLogin from "../../../src/views/components/sections/formLogin";
import FormRegister from "../../../src/views/components/sections/formRegister.jsx";

function NonAuthenticatedComponent({withSpinner}){

    
    const [navBarContent,setNavBarContent]=useState(false);
    const [modal1, setModal1] = useState(false);
    const [modal2, setModal2] = useState(false);
    const toggle1 = () => {
        setModal1(!modal1);
    }
    const toggle2 = () => {
        setModal2(!modal2);
    }

    
      


    return <>
    <Modal size="md" isOpen={modal1} toggle={toggle1.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle1.bind(null)}>Login Area</ModalHeader>
                            <ModalBody>
                            <FormLogin/> 
                            
                            </ModalBody>
                            
                        </Modal>

                        <Modal size="md" isOpen={modal2} toggle={toggle2.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle2.bind(null)}>Register Area</ModalHeader>
                            <ModalBody>
                            <FormRegister />  
                            
                            </ModalBody>
                            
                        </Modal>
                        <NavItem>{withSpinner ? "" : <Spinner className="mx-2 text-warning"/>}<Button type="submit" className="btn btn-success waves-effect waves-light m-r-10" onClick={toggle2.bind(null)}>Register</Button>
                                <Button className="btn btn-inverse waves-effect waves-light" type="button" onClick={toggle1.bind(null)} >Sign in</Button></NavItem>
    </>
}
export default NonAuthenticatedComponent;