import {React,useEffect, useState,useContext } from "react";
import axios from "axios"
import { useLocation } from "react-router-dom";
import moment from 'moment';
import {Alert, Button,Input,Card, CardTitle, CardText, Row, Col,Toast,ToastHeader,ToastBody,Container } from 'reactstrap';
// core components
import Header from "../../components/header/header.jsx";
import Footer from "../../components/footer/footer.jsx";

function TicketDetails(){
    const location = useLocation()
  const params = new URLSearchParams(location.search)
  const [ticket,setTicket]=useState([]);
  const [ticketReply,setTicketReply]=useState([]);
  const [contentReply,setContentReply]=useState([]);
  const TicketId=params.get("ticketId");
  const [AuthenticationState,setAuthenticationState]=useState(false);

const authentificationVerification=async()=>{
    axios.defaults.withCredentials = true;
    await axios.get("http://154.56.57.8/api/isAuthenticated").then((response)=>{
        setAuthenticationState(true);
    })
    .catch((error)=>{
        console.log(error);
        setAuthenticationState(false);
    })
}

  const scrollToTicket = () => {
    const element = document.getElementById("contenuduticket");
    if(element!=null){
     
    setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100);
    }
};



  const ticketList=async()=>{

    await axios.get("http://154.56.57.8/api/getTicketAndReplies",{params:{idTicket:TicketId}})
    .then((response)=>{
        setTicket(response.data);
        setTicketReply(response.data.ticket_replays)
        console.log(response.data);
    })
    .catch((error) => {
        console.log(error);
    });
    
    
  }

  const handleChange = (event) => {
    setContentReply(event.target.value);
  };

  const submitTicketReply=async()=>{
    axios.defaults.withCredentials = true;
    await axios.post("http://154.56.57.8/api/submitTicketReply",
    {
        contenu:contentReply,
        idTicket:TicketId,
    }).then((response)=>{
        console.log(response);
        ticketList();
        setContentReply("");
    })
    .catch((error) => {
        console.log(error);
    });
  }
  useEffect(() => {
    ticketList();
    scrollToTicket();
    authentificationVerification();
  }, []);
  return (
<>

<Header />

{!AuthenticationState? <Alert>Oups il parait que vous-avez consulter le mauvais chemin</Alert> :



            <>
            <div className="page-wrapper my-5">
            <Row className="justify-content-center mb-5">
                        <Col md="7" className="text-center">
                            <h2 className="title" id="contenuduticket">Consultation de ticket</h2>
                            <h6 className="subtitle" >Les messages sont enregistrer prire d'viter les rponses offensive</h6>
                        </Col>
                    </Row>
                <Container className="align-items-center">
                <Row className="row-grid align-items-center" >
                    <Col md="8" key={ticket.id}>
                        <Card body className="card-shadow">
                            <CardTitle><strong className="me-auto">{ticket.titre} -  </strong><span className="text-end font-10">{moment(ticket.created_at).fromNow()}</span></CardTitle>
                            <hr></hr><br></br>
                            <CardText>{ticket.contenuTicket}.</CardText>
                            
                        </Card>
                    </Col>
                    </Row>    

        {ticketReply.map(oneRep=>{
            return(
                <>
                <Row className="row-grid align-items-center my-4">
                <Col lg="8">
                <Toast key={oneRep.id} className="w-100">
                    <ToastHeader>
                        <strong class="me-auto">Rponse de : <span className="text-success">{JSON.stringify(oneRep.client.nomClient)}</span></strong>
                        <small className="text-end"> - { moment(oneRep.created_at).fromNow() }</small>
                    </ToastHeader>
                    <ToastBody>{oneRep.contenu}</ToastBody>
                </Toast>
                </Col>
                </Row>
                
                </>
            );
        })}
        <hr></hr>
        {ticket.Etat ===0 ? <><Input
        className='h-75 mx-1 my-3'
        type="textarea"
        placeholder="Ecrire une rponse"
        onChange={handleChange} value={contentReply}
      />
      
      <Button className="mx-1" onClick={submitTicketReply} >Rpondre</Button></> : ""}
                       
                </Container>
            </div>
            <Footer />
            </>
}
            </>



       
  );

}
export default TicketDetails;