/* eslint-disable */
import {React,useState,useEffect} from 'react';
import axios from 'axios';
import { Row, Col, Container, Card, CardBody } from 'reactstrap';

import img1 from '../../../assets/images/portfolio/img1.jpg';
import img2 from '../../../assets/images/portfolio/img2.jpg';
import img3 from '../../../assets/images/portfolio/img3.jpg';
import img4 from '../../../assets/images/portfolio/img4.jpg';
import img5 from '../../../assets/images/portfolio/img5.jpg';
import img6 from '../../../assets/images/portfolio/img6.jpg';

const PortfolioComponent = () => {

    const [ourWork, setourWork] = useState([]);
    const getOurWork = async () => {
      const { data } = await axios.get("http://154.56.57.8/api/ourWork");
      setourWork(data);
    }
  
    useEffect(() => {
      getOurWork();
    }, []);

    return (
        <div>
            
            <div className="spacer">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h2 className="title">Our Amazing Recent Work </h2>
                            <h6 className="subtitle">You can relay on our amazing features list and also our customer services will be great experience for you without doubt and in no-time</h6>
                        </Col>
                    </Row>
                    <Row className="m-t-40">




                    {ourWork.map((work) => (
                        <Col md="4" key={work.id}>
                        <Card className="card-shadow">
                            <a href="#" className="img-ho"><img className="card-img-top" src={require("../../../assets/images/portfolio/"+work.images_url)} alt={work.nom_site} /></a>
                            <CardBody>
                                <h5 className="font-medium m-b-0">{work.description_site}</h5>
                                <p className="m-b-0 font-14">{work.type_site}</p>
                            </CardBody>
                        </Card>
                    </Col>
        ))}
                        
                    </Row>
                </Container>
            </div>
        </div>
    );
}

export default PortfolioComponent;
