import React, { useState } from 'react';
import { Button,Input,Row,Col,Alert } from 'reactstrap';
import axios from 'axios';

const ReviewForm = () => {
  const [content, setContent] = useState('');
  const [rating, setRating] = useState(1);
  const [reviewSended,setreviewSended]=useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();

    const data = {
      content,
      rating,
    };

    await axios.post('http://154.56.57.8/api/RegisterReview', data).then((response)=>{
        console.log(response);
        setreviewSended(true);
    })

    setContent('');
    setRating(1);
  };

  return (
    <div>
    {reviewSended ? <Alert>Nous avons Bien reu votre Commentaire ! Cliquez <a href="/">ici</a> pour retourner  la page d'accueil</Alert> :
    
    <><h2>Votre commentaire
        <br></br><small className='font-10'>Ecrire quelque chose de beau :)</small>
      </h2>
      <Input
        className='h-75'
        type="textarea"
        placeholder="Votre review"
        onChange={(e) => setContent(e.target.value)}
      />
      <div className="stars text-center w-100 my-3">
        <Row className='w-50'>
        <Col>
        <Input
        className='bg-success'
          type="radio"
          name="rating"
          value="1"
          checked={rating === 1}
          onChange={(e) => setRating(1)}
        />
        </Col>
        <Col>
        <div className="font-15">
          <a href="" className="text-success"><i className="fa fa-star"></i></a>
           <a href="" className="text-muted"><i className="fa fa-star"></i></a>
             <a href="" className="text-muted"><i className="fa fa-star"></i></a>
              <a href="" className="text-muted"><i className="fa fa-star"></i></a>
               <a href="" className="text-muted"><i className="fa fa-star"></i></a>
          </div>
          </Col>
          </Row>
          <Row className='w-50'>
            <Col>
        <Input
        className='bg-success'
          type="radio"
          name="rating"
          value="2"
          checked={rating === 2}
          onChange={(e) => setRating(2)}
        />
        </Col>
        <Col>
        <div className="font-15">
          <a href="" className="text-success"><i className="fa fa-star"></i></a>
           <a href="" className="text-success"><i className="fa fa-star"></i></a>
             <a href="" className="text-muted"><i className="fa fa-star"></i></a>
              <a href="" className="text-muted"><i className="fa fa-star"></i></a>
               <a href="" className="text-muted"><i className="fa fa-star"></i></a>
          </div>
          </Col>
          </Row>
          <Row className='w-50'>
            <Col>
        <Input
        className='bg-success'
          type="radio"
          name="rating"
          value="3"
          checked={rating === 3}
          onChange={(e) => setRating(3)}
        />
        </Col>
        <Col>
        <div className="font-15">
          <a href="" className="text-success"><i className="fa fa-star"></i></a>
           <a href="" className="text-success"><i className="fa fa-star"></i></a>
             <a href="" className="text-success"><i className="fa fa-star"></i></a>
              <a href="" className="text-muted"><i className="fa fa-star"></i></a>
               <a href="" className="text-muted"><i className="fa fa-star"></i></a>
          </div>
          </Col>
          </Row>
          <Row className='w-50'>
            <Col>
        <Input
        className='bg-success'
          type="radio"
          name="rating"
          value="4"
          checked={rating === 4}
          onChange={(e) => setRating(4)}
        />
        </Col>
        <Col>
        <div className="font-15">
          <a href="" className="text-success"><i className="fa fa-star"></i></a>
           <a href="" className="text-success"><i className="fa fa-star"></i></a>
             <a href="" className="text-success"><i className="fa fa-star"></i></a>
              <a href="" className="text-success"><i className="fa fa-star"></i></a>
               <a href="" className="text-muted"><i className="fa fa-star"></i></a>
          </div>
          </Col>
          </Row>
          <Row className='w-50'>
            <Col>
        <Input
        className='bg-success'
          type="radio"
          name="rating"
          value="5"
          checked={rating === 5}
          onChange={(e) => setRating(5)}
        />
        </Col>
        <Col>
        <div className="font-15">
          <a href="" className="text-success"><i className="fa fa-star"></i></a>
           <a href="" className="text-success"><i className="fa fa-star"></i></a>
             <a href="" className="text-success"><i className="fa fa-star"></i></a>
              <a href="" className="text-success"><i className="fa fa-star"></i></a>
               <a href="" className="text-success"><i className="fa fa-star"></i></a>
          </div>
          </Col>
          </Row>
      </div>
      <hr></hr>
      <Button onClick={handleSubmit}>Submit</Button>
      </>
    }
    </div>

  );
};

export default ReviewForm;