/* eslint-disable */
import axios from 'axios';
import {React,useState,useEffect} from 'react';
import { useNavigate } from "react-router-dom";
import {Alert, Button,Form, Spinner } from 'reactstrap';
import { Row, Col, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Container, NavbarBrand, Navbar, Nav, NavItem, NavLink, NavbarToggler, Collapse,Modal, ModalHeader, ModalBody } from 'reactstrap';
import banner from '../../../assets/images/form-banners/banner1/banner-img.png';
import logo3 from '../../../assets/images/logos/winco-logo.png';
import {Helmet} from "react-helmet";
import NonAuthenticatedComponents from '../../../components/Authentication/NonAuthenticatedComponents.jsx'
import AuthenticatedComponent from "../../../components/Authentication/AuthenticatedComponents";
const HeaderComponent =  (props) => {
    const [navBarContent,setNavBarContent]=useState(false);
    const [withSpinner,setWithSpinner]=useState(false);
    const [isOpen, setIsOpen] = useState(false);
    const toggle = () => setIsOpen(!isOpen);
    const SectionsArray = ["NosPlans", "NosServices", "AboutUs","ContactUs","Accueil"];
    const [clientInfo, setinfoclient]=useState("");
    const [siteClient, setSiteClient]=useState([]);
    const [commandeClient, setCommandeClient]=useState([]);
    const [emailSubscriber, setSubscriber]=useState("");
    const [confirmationSub,setConfirmationSub]=useState(true);
    const [modal1, setModal1] = useState(false);
    const toggle1 = () => {
        setModal1(!modal1);
    }
    axios.defaults.withCredentials = true;
    const navigate = useNavigate();

    

    const handleInputChange =(evt)=>{
        setSubscriber(evt.target.value);
    }


    const NewsLettre=async(event)=>{
        axios.defaults.withCredentials = true;
        event.preventDefault();
        await axios
        .post("http://154.56.57.8/api/subscribe",{emailSubscriber:emailSubscriber}).then((response)=>{
              console.log(response);
              setConfirmationSub(true);
         })
         .catch((error) => {
            console.log(error);
            setConfirmationSub(false);
        });
    }

    
     

    const authentificationVerification=async()=>{
        axios.defaults.withCredentials = true;
        await axios
        .get("http://154.56.57.8/api/isAuthenticated")
        .then((response) => {
            setWithSpinner(true);
          console.log(response);
          setinfoclient(JSON.stringify(response.data.email));
            setNavBarContent(true);

            axios
            .get("http://154.56.57.8/api/Sites").then((response)=>{
                  console.log(response);
                  setSiteClient(response.data);
             })
             .catch((error) => {
                console.log(error);
               
            });

            axios
            .get("http://154.56.57.8/api/getCommandes").then((response)=>{
                  console.log(response);
                  setCommandeClient(response.data);
             })
             .catch((error) => {
                console.log(error);
               
            });

        })
        .catch((error) => {
            setWithSpinner(true);
            axios.defaults.withCredentials = true;
            axios.get('http://154.56.57.8/api/sanctum/csrf-cookie').then(response => {
            console.log(response);

            });
        });
    }
     



        

        

        useEffect(() => {
            authentificationVerification();
           
          }, []);
        

    const scrollToAccueil = () => {
        const element = document.getElementById(SectionsArray[4]);
        if(element==null){navigate("/");}
        else{setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100)};
      };
    const scrollToPlans = () => {
        const element = document.getElementById(SectionsArray[0]); 
        if(element==null){navigate("/");}
       else{ setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100)};
      };
      const scrollToServices = () => {
        const element = document.getElementById(SectionsArray[1]); 
        if(element==null){navigate("/");}
        else{setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100)};
      };
      const scrollToAboutUs = () => {
        const element = document.getElementById(SectionsArray[2]); 
        if(element==null){navigate("/");}
        else{setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100);}
      };
      const scrollToContactUs = () => {
        const element = document.getElementById(SectionsArray[3]);
        if(element==null){navigate("/");}
        else{setTimeout(function () { element.scrollIntoView({ behavior: 'smooth',block: "start" });},100);}
      };

    return (

        
        <div id="section">
            
            <Helmet>
            @csrf
            <meta name="csrf-token" content="{{ csrf_token() }}"/>
            </Helmet>
            
            <div className="header1 po-relative bg-light">
                <Container>
                    <Navbar className="navbar-expand-lg h2-nav fixed-top  bg-light">
                        <NavbarBrand href="#"><img src={logo3} alt="wrapkit" /></NavbarBrand>
                        <Collapse isOpen={isOpen} navbar id="header1">
                            <Nav navbar className="ms-auto me-4 mt-2 mt-lg-0"> 
                                <NavItem className="active" onClick={scrollToAccueil}><NavLink href="#">Accueil</NavLink></NavItem>
                                <NavItem onClick={scrollToPlans}><NavLink href="#">Nos Plans</NavLink></NavItem>
                                <NavItem onClick={scrollToServices}><NavLink href="#">Nos Services</NavLink></NavItem>
                                <NavItem onClick={scrollToAboutUs}><NavLink href="#"> Propos</NavLink></NavItem>
                                <NavItem onClick={scrollToContactUs}><NavLink href="#">Contact</NavLink></NavItem>
                                
                                {navBarContent ?  <AuthenticatedComponent emailConnected={clientInfo} sitesConnected={siteClient} commandesConnected={commandeClient} /> : <><NonAuthenticatedComponents withSpinner={withSpinner}/></>}
                                
 


                                
                            </Nav>
                        </Collapse>
                    </Navbar>
                </Container>
            </div>
            <div className="bg-light">
                <section>
                    
                    <div id="banner1" className="banner spacer">
                        <Container>
                            <Row>
                                <Col lg="5" md="7" className="align-self-center">
                                    <h2 className="title font-bold">Give your Business Real boost Now!</h2>
                                    <p className="m-t-40 m-b-30">To accomplish great things, we must not only act, but also dream; not only plan, but also believe.</p>
                                    <Form className="m-t-40 " onSubmit={NewsLettre} method="POST">
                                        <input type="text" name="emailSubscriber" id="emailSubscriber" value={emailSubscriber} placeholder="Enter Email Address" className="font-16" onChange={handleInputChange}/>
                                        <input type="submit" value="Get Started" className="bg-success-gradiant font-semibold font-16 btn-rounded text-uppercase text-white text-center" onClick={toggle1.bind(null)}/>
                                    </Form>
                                </Col>
                                <Col lg="6" md="5" className="align-self-center ms-auto">
                                <img src={banner} alt="We are Digital Agency" className="img-fluid" />
                                </Col>
                            </Row>
                        </Container>
                    </div>
                </section>
            </div>


            <Modal size="md" isOpen={modal1} toggle={toggle1.bind(null)} >
                            
                            
                            <ModalHeader toggle={toggle1.bind(null)}>Confirmation</ModalHeader>
                            <ModalBody>
                            
                            {confirmationSub ? <><Alert>Flicitation vous tes maintenant abonner  notre newsletter</Alert></> : <Alert className='alert-danger'>Erreur vous ne pouvez pas ou vous avez dja rejoindre notre newsletter</Alert> }
                            </ModalBody>
                            
            </Modal>
        </div>
    );
}

export default HeaderComponent;
