/* eslint-disable */
import {React,useState,useEffect} from 'react';
import { Row, Col, Container, Card, CardBody } from 'reactstrap';
import axios from 'axios';
import img1 from '../../../assets/images/testimonial/1.jpg';
import img2 from '../../../assets/images/testimonial/2.jpg';
import img3 from '../../../assets/images/testimonial/3.jpg';

const TestimonialComponent = () => {

    const [clientsReviews,setClientReviews]=useState([]);

    const getClientsReviews= async()=>{
        await axios
        .get("http://154.56.57.8/api/Reviews").then((response)=>{
              setClientReviews(response.data);
         })
         .catch((error) => {
            console.log(error);
        });
    }
    useEffect(() => {
        getClientsReviews();
      }, []);

    return (
        <div>
            
            

            <div className="testimonial3 spacer bg-light">
                <Container>
                    <Row className="justify-content-center">
                        <Col md="7" className="text-center">
                            <h2 className="title">Check what our Customers are Saying</h2>
                            <h6 className="subtitle">You can relay on our amazing features list and also our customer services will be great experience for you without doubt and in no-time</h6>
                        </Col>
                    </Row>

                    <Row className="testi3 m-t-40 justify-content-center">
        {clientsReviews.map((review) => (

          <Col lg="4" md="6" key={review.id}>
                            <Card className="card-shadow">
                                <CardBody>
                                    <h6 className="font-light m-b-30">{review.content}</h6>
                                    <div className="d-flex no-block align-items-center">
                                        <span className="thumb-img"><i className="fa fa-user fa-2x mx-2 text-success"></i></span>
                                        <div className="m-l-20">
                                            <h6 className="m-b-0 customer">{review.clients.nomClient} {review.clients.prenomClient}</h6>
                                            <div className="font-10">
                                                {review.rating > 0 ? <> <i className="fa fa-star text-success"></i></> :  <i className="fa fa-star text-muted"></i>}
                                                {review.rating > 1 ? <> <i className="fa fa-star text-success"></i></> :  <i className="fa fa-star text-muted"></i>}
                                                {review.rating > 2 ? <> <i className="fa fa-star text-success"></i></> :  <i className="fa fa-star text-muted"></i>}
                                                {review.rating > 3 ? <> <i className="fa fa-star text-success"></i></> :  <i className="fa fa-star text-muted"></i>}
                                                {review.rating > 4 ? <> <i className="fa fa-star text-success"></i></> :  <i className="fa fa-star text-muted"></i>}
                                            </div>
                                        </div>
                                    </div>
                                </CardBody>
                            </Card>
                        </Col>
      ))}
      </Row>
                    
                </Container>
            </div>
        </div>
    );
}

export default TestimonialComponent;
